video {
    object-fit: cover;
    width: 100%;
    min-height: 100vh;
    position: absolute;
    z-index: -100;
    overflow: hidden;

}

.text-anim {
    color: #f0566d !important;
}

.hero-container {
    /* background: url('../images/space1.jpeg') center center/cover no-repeat; */
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.2);
    object-fit: contain;
}

.hero-container>h1 {
    color: white;
    font-size: 100px;
    margin-top: -100px;
}


.hero-container>p {
    color: white;
    font-size: 40px;
    margin-top: 20px;
}

.btn {
    width: 100%;

}

@media screen and (max-width: 960px) {
    .hero-container>h1 {
        font-size: 70px;
        margin-top: -150px;
    }
}

@media screen and (max-width: 768px) {
    .hero-container>h1 {
        font-size: 50px;
        margin-top: -100px;
    }

    .hero-container>p {
        font-size: 30px;
    }

    .btn {
        width: 100%;

    }
}